import React, { useState } from 'react'
import { FaChevronLeft, FaChevronRight, FaQuoteRight } from 'react-icons/fa'
import data from './data'
const Review = () => {
  const [index, setIndex] = useState(0)
  const { id, name, job, image, text } = data[index]

  const checkNumber = (number) => {
    if (number < 0) {
      return (number = data.length - 1)
    }
    if (number > data.length - 1) {
      return (number = 0)
    }
    return number
  }

  const handleLeft = () => {
    setIndex((prevIndex) => {
      let newIndex = prevIndex - 1
      return checkNumber(newIndex)
    })
  }
  const handleRight = () => {
    setIndex((prevIndex) => {
      let newIndex = prevIndex + 1
      return checkNumber(newIndex)
    })
  }
  const random = () => {
    let rand = Math.floor(Math.random() * data.length)
    if (rand === index) {
      rand = index + 1
    }
    setIndex(checkNumber(rand))
    console.log(rand)
  }
  // const [review, setReview] = useState(data[index])
  return (
    <>
      <main className='container'>
        <div className='row '>
          <div className='col-md-5 m-auto'>
            <h1 className='text-center text-light mt-5 mb-0'>our Review</h1>
            <div className='underline mb-5 mx-auto'></div>
            <div className='card rounded-5 my-5 text-center '>
              <div className='card-body'>
                {/* <FaQuoteRight className='qoute-icon' /> */}
                <div className='image text-center'>
                  <img src={image} alt={name} />
                </div>
                <h4 className='name'>{name}</h4>
                <p className='text-primary text-uppercase job'>{job}</p>
                <p className='text'>{text}</p>
                <button className='btn mr-2 text-primary' onClick={handleLeft}>
                  <FaChevronLeft />
                </button>
                <button className='btn text-primary mr2' onClick={handleRight}>
                  <FaChevronRight />
                </button>
                <br />
                <button
                  className='mt-4 btn btn-outline-primary'
                  onClick={random}
                >
                  random quote
                </button>
              </div>
            </div>
            {/* </>
              )
            })} */}
          </div>
        </div>
      </main>
    </>
  )
}

export default Review
